___CSS_LOADER_EXPORT___.push([module.id, "#germinate{align-items:center;display:flex;justify-content:center;min-height:100vh}#germinate svg{fill:#ced4da;height:32px;vertical-align:top;width:32px}#germinate .table.is-setup{background-color:#fff;box-shadow:0 2px 3px hsla(0,0%,4%,.1),0 0 0 1px hsla(0,0%,4%,.1);font-size:80%;max-width:400px}#germinate .table.is-setup tbody td{border-left:1px solid #ced4da}#germinate .table.is-setup tfoot td,#germinate .table.is-setup tfoot th{border-color:transparent;vertical-align:bottom}", "",{"version":3,"sources":["webpack://./resources/assets/js/components/Germinate.vue","webpack://./resources/assets/sass/01_settings/_variables.scss"],"names":[],"mappings":"AAEI,WAGI,kBAAA,CADA,YAAA,CAEA,sBAAA,CAHA,gBAER,CAEQ,eAII,YCuBH,CDzBG,WAAA,CADA,kBAAA,CAEA,UACZ,CAEQ,2BAEI,qBCcH,CDZG,gEAAA,CADA,aAAA,CAFA,eAGZ,CAOgB,oCACI,6BALpB,CASgB,wEACI,wBAAA,CACA,qBAPpB","sourcesContent":["\n    @import '~GlobalSass';\n    #germinate {\n        min-height: 100vh;\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        svg {\n            vertical-align: top;\n            height: 32px;\n            width: 32px;\n            fill: $grey-4;\n        }\n        .table.is-setup {\n            max-width: 400px;\n            background-color: $white;\n            font-size: 80%;\n            box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);\n            thead {\n                // th, td {\n                //     background-color: $grey-0;\n                // }\n            }\n            tbody {\n                td {\n                    border-left: 1px solid $grey-4;\n                }\n            }\n            tfoot {\n                td, th {\n                    border-color: transparent;\n                    vertical-align: bottom;\n                }\n            }\n        }\n    }\n","//\n// Z-index.\n$z-top   : 3000;\n$z-middle: 2000;\n$z-bottom: 1000;\n\n// Miscellaneous.\n$easing: cubic-bezier(0.57, -0.24, 0.49, 1.56);\n$speed : 0.45s;\n\n// Transitions.\n$transition: all $speed $easing;\n\n//\n// Spacing.\n$space-xsmall: 0.25rem;\n$space-small : 0.5rem;\n$space       : 1rem;\n$space-large : 1.25rem;\n$space-xlarge: 1.5rem;\n\n//\n// Radius.\n$radius-small: 2px;\n$radius      : 3px;\n$radius-large: 5px;\n\n//\n// Greyscale.\n$white : #fff;\n$grey-0: #f8f9fa;\n$grey-1: #f1f3f5;\n$grey-2: #e9ecef;\n$grey-3: #dee2e6;\n$grey-4: #ced4da;\n$grey-5: #adb5bd;\n$grey-6: #868e96;\n$grey-7: #495057;\n$grey-8: #343a40;\n$grey-9: #212529;\n$black : #0a0a0a;\n\n//\n// Greyscale usage.\n$grey-lightest: $grey-2;\n$grey-lighter : $grey-3;\n$grey-light   : $grey-4;\n$grey         : $grey-5;\n$grey-dark    : $grey-6;\n$grey-darker  : $grey-7;\n$grey-darkest : $grey-8;\n\n//\n// Colors.\n$red      : #fa5252;\n$pink     : #e64980;\n$purple   : #be4bdb;\n$violet   : #7950f2;\n$indigo   : #4c6ef5;\n$blue     : #228ae6;\n$cyan     : #15aabf;\n$teal     : #12b886;\n$turquoise: #00d1b2;\n$green    : #40c057;\n$lime     : #82c91e;\n$yellow   : #fab005;\n$orange   : #fd7e14;\n\n//\n// Color usage.\n$primary: $indigo;\n$info   : $blue;\n$success: $green;\n$warning: $yellow;\n$danger : $red;\n$colors: (\n    \"white\"  : $white,\n    \"grey\"   : $grey,\n    \"dark\"  : $grey-8,\n    \"black\"  : $black,\n    \"primary\": $primary,\n    \"info\"   : $info,\n    \"success\": $success,\n    \"warning\": $warning,\n    \"danger\" : $danger\n);\n\n//\n// Brand colors.\n$brand-color-primary  : $purple;\n$brand-color-secondary: $lime;\n\n//\n// Site colors.\n$background : $grey-0;\n$text       : $grey-8;\n$text-invert: findColorInvert($text);\n$text-light : $grey;\n$text-strong: $grey-darker;\n\n//\n// Border colors.\n$border      : $grey-lighter;\n$border-hover: $grey-light;\n\n//\n// Link colors.\n$link        : $brand-color-primary;\n$link-visited: $brand-color-primary;\n$link-hover  : darken($brand-color-primary, 10%);\n$link-active : darken($brand-color-primary, 10%);\n\n//\n// Typography.\n$family-sans-serif: BlinkMacSystemFont, -apple-system, \"Segoe UI\", \"Roboto\", \"Oxygen\", \"Ubuntu\", \"Cantarell\", \"Fira Sans\", \"Droid Sans\", \"Helvetica Neue\", \"Helvetica\", \"Arial\", sans-serif;\n$family-monospace: SFMono-Regular, Inconsolata, Menlo, Monaco, Consolas, \"Liberation Mono\", \"Courier New\", monospace;\n$font-family-base: $family-monospace;\n\n//\n// Font sizing.\n$size-1: 3rem;\n$size-2: 2.5rem;\n$size-3: 2rem;\n$size-4: 1.5rem;\n$size-5: 1.25rem;\n$size-6: 1rem;\n$size-7: 0.85rem;\n\n//\n// Font weight.\n$weight-light   : 300;\n$weight-normal  : 400;\n$weight-semibold: 500;\n$weight-bold    : 700;\n\n//\n// Block quotes.\n$open-blockquote : '\"';\n$close-blockquote: '\"';\n\n//\n// Breakpoints.\n$breakpoints: (\n    baby    : 544px,\n    toddler : 768px,\n    teenager: 992px,\n    adult   : 1200px,\n    senior  : 1400px,\n);\n"],"sourceRoot":""}]);
